'use client';

import React, { useState, useEffect } from 'react';
import { addEmployee, getEmployees } from '../lib/data';

export default function TestFirestoreIntegration() {
  const [employees, setEmployees] = useState<any[]>([]);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');

  const [newEmployee, setNewEmployee] = useState({
    name: '',
    email: '',
    phone: '',
    role: 'Developer',
    department: 'Engineering',
    address: ''
  });

  const loadEmployees = async () => {
    try {
      setLoading(true);
      const data = await getEmployees();
      setEmployees(data);
      setMessage(`Loaded ${data.length} employees from Firestore`);
    } catch (error) {
      setMessage(`Error loading employees: ${error}`);
      console.error('Error:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleAddEmployee = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!newEmployee.name || !newEmployee.email || !newEmployee.phone || !newEmployee.address) {
      setMessage('Please fill in all fields');
      return;
    }

    try {
      setLoading(true);
      setMessage('Adding employee to Firestore...');
      
      const employee = await addEmployee(newEmployee);
      setMessage(`âœ… Employee "${employee.name}" added successfully to Firestore!`);
      
      // Reset form
      setNewEmployee({
        name: '',
        email: '',
        phone: '',
        role: 'Developer',
        department: 'Engineering',
        address: ''
      });
      
      // Reload employees
      await loadEmployees();
    } catch (error) {
      setMessage(`âŒ Error adding employee: ${error}`);
      console.error('Error:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadEmployees();
  }, []);

  return (
    <div className="max-w-4xl mx-auto p-6 bg-white">
      <h1 className="text-3xl font-bold mb-6 text-center">ðŸ”¥ Firestore Integration Test</h1>
      
      {/* Status Message */}
      <div className="mb-6 p-4 bg-gray-100 rounded-lg">
        <p className="text-sm">
          <strong>Status:</strong> {message || 'Ready to test Firestore integration'}
        </p>
        {loading && (
          <div className="mt-2">
            <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-500 inline-block"></div>
            <span className="ml-2 text-sm">Loading...</span>
          </div>
        )}
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Add Employee Form */}
        <div className="bg-blue-50 p-4 rounded-lg">
          <h2 className="text-xl font-semibold mb-4">âž• Add New Employee (Direct to Firestore)</h2>
          <form onSubmit={handleAddEmployee} className="space-y-3">
            <input
              type="text"
              placeholder="Name"
              value={newEmployee.name}
              onChange={(e) => setNewEmployee(prev => ({ ...prev, name: e.target.value }))}
              className="w-full p-2 border rounded"
              required
            />
            <input
              type="email"
              placeholder="Email"
              value={newEmployee.email}
              onChange={(e) => setNewEmployee(prev => ({ ...prev, email: e.target.value }))}
              className="w-full p-2 border rounded"
              required
            />
            <input
              type="tel"
              placeholder="Phone"
              value={newEmployee.phone}
              onChange={(e) => setNewEmployee(prev => ({ ...prev, phone: e.target.value }))}
              className="w-full p-2 border rounded"
              required
            />
            <select
              value={newEmployee.role}
              onChange={(e) => setNewEmployee(prev => ({ ...prev, role: e.target.value }))}
              className="w-full p-2 border rounded"
            >
              <option value="Developer">Developer</option>
              <option value="Designer">Designer</option>
              <option value="Manager">Manager</option>
              <option value="Admin">Admin</option>
            </select>
            <select
              value={newEmployee.department}
              onChange={(e) => setNewEmployee(prev => ({ ...prev, department: e.target.value }))}
              className="w-full p-2 border rounded"
            >
              <option value="Engineering">Engineering</option>
              <option value="Design">Design</option>
              <option value="Marketing">Marketing</option>
              <option value="Sales">Sales</option>
            </select>
            <input
              type="text"
              placeholder="Address"
              value={newEmployee.address}
              onChange={(e) => setNewEmployee(prev => ({ ...prev, address: e.target.value }))}
              className="w-full p-2 border rounded"
              required
            />
            <button
              type="submit"
              disabled={loading}
              className="w-full bg-blue-500 text-white p-2 rounded hover:bg-blue-600 disabled:opacity-50"
            >
              {loading ? 'Adding to Firestore...' : 'Add Employee to Firestore'}
            </button>
          </form>
        </div>

        {/* Employees List */}
        <div className="bg-gray-50 p-4 rounded-lg">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-semibold">ðŸ‘¥ Employees from Firestore ({employees.length})</h2>
            <button
              onClick={loadEmployees}
              disabled={loading}
              className="bg-green-500 text-white px-3 py-1 rounded text-sm hover:bg-green-600 disabled:opacity-50"
            >
              Refresh
            </button>
          </div>
          
          {employees.length === 0 ? (
            <div className="text-center py-8 text-gray-500">
              <p>No employees found in Firestore</p>
              <p className="text-sm mt-2">Add an employee above to test the integration</p>
            </div>
          ) : (
            <div className="space-y-3 max-h-96 overflow-y-auto">
              {employees.map((employee) => (
                <div key={employee.id} className="bg-white p-3 rounded border">
                  <div className="flex justify-between items-start">
                    <div className="flex-1">
                      <h3 className="font-medium">{employee.name}</h3>
                      <p className="text-sm text-gray-600">{employee.email}</p>
                      <p className="text-sm text-gray-500">{employee.phone}</p>
                      <p className="text-sm text-gray-500">{employee.role} - {employee.department}</p>
                      <p className="text-sm text-gray-400">{employee.address}</p>
                      {employee.createdAt && (
                        <p className="text-xs text-gray-400">
                          Added: {new Date(employee.createdAt.toDate()).toLocaleString()}
                        </p>
                      )}
                    </div>
                    <span className="bg-green-100 text-green-800 px-2 py-1 rounded text-xs">
                      Firestore
                    </span>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

      {/* Instructions */}
      <div className="mt-8 bg-yellow-50 p-4 rounded-lg">
        <h3 className="text-lg font-semibold mb-2">ðŸ§ª Testing Instructions</h3>
        <ol className="list-decimal list-inside space-y-1 text-sm">
          <li>Fill out the employee form above</li>
          <li>Click "Add Employee to Firestore"</li>
          <li>Watch as the employee is added directly to Firestore (no JSON files!)</li>
          <li>The employee list will automatically refresh to show the new data</li>
          <li>Check your Firebase Console to see the data in Firestore</li>
          <li>Try adding multiple employees to see real-time updates</li>
        </ol>
        
        <div className="mt-4 p-3 bg-green-100 rounded">
          <p className="text-sm font-medium text-green-800">âœ… Success Indicators:</p>
          <ul className="text-xs text-green-700 mt-1 list-disc list-inside">
            <li>Status shows "Employee added successfully to Firestore!"</li>
            <li>Employee appears in the list immediately</li>
            <li>Employee has a "Firestore" badge</li>
            <li>Data persists after page refresh</li>
          </ul>
        </div>
      </div>
    </div>
  );
}
